.advantages__title,
.advantages__subtitle  {
.font-title();
margin: 0;
text-transform: uppercase;
text-decoration: none;
}

.advantages__text, {
.font-text();
text-transform: uppercase;
color: #333333;
}
.advantages__description,
.advantages__number {
  .font-text();
  margin: 0;
  display: block
}

.advantages__advantages-item{
  .blue-color();
  list-style-type: none;
  color: #ffffff;

}
.advantages__list,
.advantages-list__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.advantages-list__item {
  list-style-type: none;
  &--house {
    background-image: url(../../icons/icon-house.svg.svg);
    background-repeat: no-repeat;
    background-position: 10px 10px 10px;
  }
  &--burger {
    background-image: url(../../icons/icon-burger.svg.svg);
    background-repeat: no-repeat;
    background-position: 10px 10px 10px;
  }
  &--gift {
    background-image: url(../../icons/icon-gift.svg.svg);
    background-repeat: no-repeat;
    background-position: 10px 10px 10px;
  }
}
.advantages-list__title  {
  margin-left: 70px;
  .font-title();
  text-align: left;
}
.advantages-list__description {
  .font-text();
  text-align: left;
  color: #333333;
  margin-left: 70px;
}
.advantages__item {
  background-color: #EEEEEE;
  color: #333333;
}
